html {
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
}

body {
  min-width: 320px;
  margin: 0;
  padding: 0;

  font-size: 16px;
  line-height: 21px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: $darkColor;
  text-transform: none;

  background-color: #ffffff;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  clip: rect(0 0 0 0);
  width: -1px;
  height: 1px;
  margin: -1px;
  font-size: 0;
}
